import React, { useState } from 'react'
import { ThemeProvider as SCThemeProvider } from 'styled-components'
import { light, dark } from '@pancakeswap-libs/uikit'

const CACHE_KEY = 'IS_DARK'

export interface ThemeContextType {
  isDark: boolean;
  toggleTheme: () => void;
}

const ThemeContext = React.createContext<ThemeContextType>({ isDark: false, toggleTheme: () => null })

const ThemeContextProvider: React.FC = ({ children }) => {
  const [isDark, setIsDark] = useState(() => {
    // const isDarkUserSetting = localStorage.getItem(CACHE_KEY)
    // return isDarkUserSetting ? JSON.parse(isDarkUserSetting) : true
    return true;
  })

  const toggleTheme = () => {
    setIsDark((prevState: any) => {
      localStorage.setItem(CACHE_KEY, JSON.stringify(!prevState))
      return !prevState || true;
    })
  }

  const tempDark = { ...dark, 
    nav: {...dark.nav, background: "rgb(39, 38, 44)"},
    colors: {...dark.colors, text: "#FF720D", background: '#100c18', card: '#000'},
    toggle: {...dark.toggle, handleBackground: "#100c18"},
    card: {...dark.card, background: "#000"},
    button: {...dark.button, primary: {...dark.button.primary, background: "#0056ac"}},
    // colors: { ...dark.colors, primary: "#0056AC", textSubtle: "#ff8800", backgroundAlt: "#000", secondary: "#ff8800", text: "#ff8800", gradients: { ...dark.colors.gradients, bubblegum: 'linear-gradient(139.73deg,#ffffff00 0%,#ffffff00 100%)', cardHeader:'linear-gradient(139.73deg,#ffffff00 0%,#ffffff00 100%)' } }, card: { ...dark.card, background: "#000" } 
  }
  // console.log(dark);

  return (
    <ThemeContext.Provider value={{ isDark, toggleTheme }}>
      <SCThemeProvider theme={isDark ? tempDark : light}>{children}</SCThemeProvider>
    </ThemeContext.Provider>
  )
}

export { ThemeContext, ThemeContextProvider }
